import "../assets/scss/style.scss";
import "../assets/scss/form.scss";
const form = document.querySelector("form");
let errors = [];
const error = document.querySelector(".error-text");
const textarea = document.querySelector(".group-form textarea");
const nbreCaractere = document.querySelector(".nbre-caractere");

textarea.addEventListener("input", (event) => {
  const size = event.target.value.length;
  const text = event.target.value;

  if (text.match(/[\w\W]{1000}/)) {
    const val = text.match(/[\w\W]{1000}/);
    console.log(val);
    event.target.value = val[0];
    nbreCaractere.textContent = `${val[0].length}/1000 caractères max.`;
    if (nbreCaractere.classList.contains("text-novalid")) {
      nbreCaractere.classList.remove("text-novalid");
      nbreCaractere.classList.add("text-valid");
    } else {
      nbreCaractere.classList.add("text-valid");
    }
  } else {
    if (nbreCaractere.classList.contains("text-valid")) {
      nbreCaractere.classList.remove("text-valid");
      nbreCaractere.classList.add("text-novalid");
    } else {
      nbreCaractere.classList.add("text-novalid");
    }
    nbreCaractere.textContent = `${size}/1000 caractères max.`;
  }
});

form.addEventListener("submit", async (event) => {
  event.preventDefault();
  /*on recupere le contenu du button a l'origine de l'envoi du formulaire
  avec la propriété submitter
  */
  const submitButton = event.submitter.textContent;
  console.log(event.submitter.textContent);
  //on fonction  du contenu du button on execute une action annuler/enregister
  if (submitButton === "Enregistrer") {
    const data = new FormData(form);
    const article = Object.fromEntries(data.entries());
    if (validation(article)) {
      try {
        const json = JSON.stringify(article);
        const response = await fetch("https://restapi.fr/api/articleOdilon", {
          method: "POST",
          body: json,
          headers: {
            "Content-Type": "application/json",
          },
        });
        if (response.status === 200) {
          const createArticle = await response.json();
          console.log(createArticle);
          error.innerHTML = "";
          errors.length = 0;
        } else {
          if (errors.length === 0) {
            const erreurServeur = await response.json();
            errors.push(erreurServeur);
            console.log("erreur se:", errors);
            errors.forEach((element) => {
              error.innerHTML = `<il>Erreur serveur :${element}</il>`;
            });
          }
        }
      } catch (e) {
        console.log("erreur: ", e);
      }
    }
  } else if (submitButton === "Annuler") {
    const input = document.querySelectorAll(".group-form input");
    textarea.value = "";
    nbreCaractere.textContent = "";
    input.forEach((element) => (element.value = ""));
    if (errors.length) {
      errors.length = 0;
      error.innerHTML = "";
    }
  }
});

const validation = (article) => {
  if (!article.author || !article.content || !article.category) {
    errors.length = 0;
    errors.push("Vous devez renseigner tous les champs");
    errors.forEach((element) => (error.innerHTML = `<li>${element}</li>`));
    return false;
  } else {
    console.log("continue");
    errors.length = 0;
    validAuthor(article);
    validCategory(article);
    validContent(article);
    validImage(article);
    validTitle(article);
    console.log(errors);
  }
  if (errors.length === 0) {
    error.innerHTML = "";
    return true;
  } else {
    error.innerHTML = "";
    errors.forEach((element) => (error.innerHTML += `<li>${element}</li>`));
    return false;
  }
};

const validAuthor = (article) => {
  if (article.author.match(/^[a-zA-Z]+[\w\W]+$/)) {
    return true;
  } else {
    errors.push("Vous devez saisir un nom d'auteur valide");
    return false;
  }
};
const validCategory = (article) => {
  if (article.category.match(/^[a-zA-Z0-9]+[\w\W]+$/)) {
    return true;
  } else {
    errors.push("Vous devez saisir une catégorie avec des caractères valides");
    return false;
  }
};
const validTitle = (article) => {
  if (article.title.match(/^[a-zA-Z]+[\w\W]+$/)) {
    return true;
  } else {
    errors.push("Vous devez saisir un titre contenant dexs caractères valides");
    return false;
  }
};
const validImage = (article) => {
  if (article.image.match(/[\w\W]+\.{1}(?<extension>jpeg|jpg|png)/)) {
    console.log(
      "image: ",
      article.image.match(/[\w\W]+\.{1}(?<extension>jpeg|jpg|png)/)
    );
    return true;
  } else {
    errors.push("Vous devez saisir un lien contenat un format image");
    return false;
  }
};
const validContent = (article) => {
  article.content = `${article.content}`;
  if (article.content.match(/^[a-zA-Z0-9]+[\w\W.]+$/)) {
    if (article.content.length < 10) {
      errors.push("l'article doit contenir au moins 10 caractères");
      return false;
    } else {
      return true;
    }
  } else {
    errors.push("l'article doit contenir des caractères valides");
    return false;
  }
};
